const isLocal = window.location.hostname === 'localhost';

export const GEO_API_URL = isLocal
  ? 'http://localhost:8888/.netlify/functions/geo-proxy'
  : import.meta.env.VITE_GEO_API_URL;

export const geoApiOptions = {
  method: 'GET',
  headers: {
    'X-RapidAPI-Key': import.meta.env.VITE_GEO_API_KEY,
    'X-RapidAPI-Host': 'wft-geo-db.p.rapidapi.com'
  }
};

export const METEOMATICS_URL = isLocal
  ? 'http://localhost:8888/.netlify/functions/weather-proxy'
  : import.meta.env.VITE_METEOMATICS_URL;

export const meteomaticsAuth = {
  username: import.meta.env.VITE_METEOMATICS_USER,
  password: import.meta.env.VITE_METEOMATICS_PASS
};
